import { request } from 'utils';

const prefix = '/api/categories';

export const getListTypeOfReward = (page = 1, IsPagingEnabled) =>
  request({
    prefix,
    url: '/BonusType',
    params: {
      page,
      IsPagingEnabled,
      hasBTId: true,
    },
    method: 'GET',
  });
export const getTypeOfReward = id =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    method: 'GET',
  });
export const deleteTypeOfReward = id =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    method: 'DELETE',
  });
export const updateTypeOfReward = (id, values) =>
  request({
    prefix,
    url: `/BonusType/${id}`,
    data: {
      id: values.id,
      label: values.code,
      btId: values.btId,
    },
    method: 'PUT',
  });

export const createTypeOfReward = values => {
  // eslint-disable-next-line radix
  const idTypeOfReward = parseInt(values.btId);
  return request({
    prefix,
    url: '/BonusType',
    data: {
      label: values.code,
      btId: idTypeOfReward,
    },
    method: 'POST',
  });
};



