import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, message } from 'antd';
import { getListTypeOfReward, deleteTypeOfReward } from 'services/typeOfReward';

class ListTypeOfRewardsPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      typeOfReward: [],
      loading: false,
      pagination: {},
    };
  }
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListTypeOfReward(page, true)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        console.log('=======>', res.data.items);
        this.setState({
          typeOfReward: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteTypeOfReward(e)
      .then(() => {
        message.success('Xóa loại khen thưởng thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.reason);
      });
  };

  render() {
    const { pagination, typeOfReward, loading } = this.state;
    const columns = [
      {
        title: 'Cấp khen thưởng',
        dataIndex: 'parentLabel',
        key: 'parentLabel',
      },
      {
        title: 'Loại khen thưởng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          typeOfReward.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#typeOfRewards/form/edit/${record.id}`} />
              <Popconfirm 
                title="Bạn có muốn xóa?" 
                okText="Xóa"
                cancelText="Hủy"
                onConfirm={() => this.confirmDelete(record.id)}
              >
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Card title="Loại khen thưởng">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/typeOfRewards/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              dataSource={typeOfReward}
              columns={columns}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeOfRewardsPage />
  </Page>
);



